<script>
import QuestionForm from './question.vue'
import { mapActions } from 'vuex';
import store from '@/store/store'
export default {
  components: {
    QuestionForm
  },
  data() {
    return {
      question: 'What is the capital of France?',
      answers: [
        { text: 'Paris' },
        { text: 'Madrid' },
        { text: 'Berlin' },
        { text: 'London' },
      ],
      correctAnswer: 'A',
      myArr: []
    }
    },
    methods: {
      ...mapActions({
